import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the PushPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-push',
  templateUrl: 'push.html',
})
export class PushPage {

  titulo: string = "";
  text: string = "";
  n : string = "";

  constructor(public navCtrl: NavController, public navParams: NavParams) {
     this.titulo = this.navParams.get("param1");
     this.text = this.navParams.get("texto");
     this.n = this.navParams.get("nuevo");
     
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PushPage');
  }

  
clickCounter() {
  if (typeof(Storage) !== "undefined") {
    // Store
    localStorage.setItem("lastname", "jorge");
    // Retrieve
    document.getElementById("result").innerHTML = localStorage.getItem("lastname");
} else {
    document.getElementById("result").innerHTML = "Error Web Storage...";
}
  }

  open(){
    document.getElementById("variable").innerHTML = window.localStorage['variable']= "Todo Poderoso";
    console.log(window.localStorage['variable']= "Todo Poderoso");
    
  }
  

}



